.footer{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-wrap:wrap;flex-wrap:wrap;font-size:13px;color:#000;background-color:#9e9e9e;padding:25px 10px}.footer__block{width:100%;max-width:300px;margin:25px 10px 0}.footer__title{text-transform:uppercase;font-size:22px;margin-bottom:15px;font-weight:400}.footer__info{font-size:14px}.blog__title{font-size:16px}.blog__item{display:-ms-flexbox;display:flex;border-bottom:1px solid #fff;padding:15px 17px}.blog__item:last-child{border-bottom:none}.blog__img-wrap{max-width:50px;margin-right:10px;-ms-flex-negative:0;flex-shrink:0}.blog__post{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center}.footer__tag{display:inline-block;font-size:11px;padding:1px 9px;color:#fff;background-color:#616161;margin-bottom:4px;transition:all .2s linear}.footer__tag:hover{-ms-transform:scale(1.13);transform:scale(1.13);background-color:#000;cursor:default}