*{margin:0;padding:0;box-sizing:border-box}body{font-family:Work Sans,sans-serif;font-weight:300;font-size:14px;line-height:1.4}ul{padding:0;margin:0}li{list-style:none}img{display:block;width:100%;height:auto}a{text-decoration:none}.title{font-size:18px;font-weight:400;text-align:center;margin-bottom:43px;margin-top:30px}.title__sub{text-align:left;margin-bottom:20px;margin-top:46px}.title-contact{margin-bottom:0}.container{max-width:1000px}.btn{padding:13px 33px;background-color:#fff;border:2px solid transparent;outline:none;cursor:pointer;transition:border .2s linear}.btn:hover{border:2px solid #ff9100}.btn-sing{font-size:15px;padding-left:23px;padding-right:23px}.btn--send{color:#fff;background-color:#000;width:100%;border:2px solid transparent}.link{font-size:16px;color:#fff;text-decoration:none;border-bottom:1px solid #ff9100;padding:3px 5px}